.zoom {
	overflow: hidden;
}

.zoom-footer {
	
	-moz-transition: all 0.3s;
	-webkit-transition: all 0.3s;
	transition: all 0.3s;
  padding: 1%;
  margin-left: 5%;
  margin-right: 5%;
}

.zoom-footer:hover {
  background-color: aliceblue;
	-moz-transform: scale(1.15);
	-webkit-transform: scale(1.15);
	transform: scale(1.15);
  border-radius: 10%;
}

.zoom a {
  border: #28a745 solid 1px;
  border-radius: 5px;
  color: #28a745;
	max-width: 100%;
	-moz-transition: all 0.3s;
	-webkit-transition: all 0.3s;
	transition: all 0.3s;
}

.zoom:hover a {
  border-radius: 5px;
  background-color: #28a745;
  color: #fff;
	-moz-transform: scale(1.15);
	-webkit-transform: scale(1.15);
	transform: scale(1.15);
}

.copyright {
	margin-top: 2%;
}

.conecte {
  width: 100vw;
  margin-left: 2%;
	margin-right: 2%;
}

.text {
  font-size: clamp(1.1em, 3vw, 2.5em);
}

@media only screen and (max-width: 455px) {
  .main-home {
    margin-top: 10%;
    margin-left: 5%;
  }
  .minhas-habilidades {
    margin-top: 10%;
  }
}

@media only screen and (min-width: 456px) and (max-width: 499px) {
  .main-home {
    margin-top: 5%;
    margin-left: 5%;
  }
  .minhas-habilidades {
    margin-top: 0%;
  }
}

@media only screen and (min-width: 500px) and (max-width: 799px) {
  .main-home {
    margin-top: 5%;
  }
  .main-home img {
    margin-left: 10%;
  }
  .media-body {
    margin-left: 10%;
  }
  .minhas-habilidades {
    margin-left: 7%;
    border-radius: 500px;
  }
  .text-footer {
    font-size: clamp(2vw);
  }
}

@media only screen and (min-width: 800px) {
  .main-home {
    margin-top: 1%;
  }
  .main-home img {
    margin-left: 10%;
  }
  .media-body {
    margin-left: 10%;
  }
  .minhas-habilidades {
    margin-left: 10%;
  }
  .text-footer {
    font-size: clamp(2vw);
  }
}
